
import HeaderBack from '../Common/HeaderBack'
import Group from 'vux/src/components/group/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import value2name from 'vux/src/filters/value2name.js'


export default {
components: {
myHeader: HeaderBack,
Group,
Checker,
CheckerItem,
XInput,
XAddress,
XTextarea,
},
data () {
return {
header: {
HBL_icon:"icon_back",
HBC_text:"添加地址",
HBR_icon: " ",
HBR_show: true,
HBR_type: "addaddress",
HBR_show_long:true,
HBR_text_long:"保存",
},
name: '',
sex: '先生',
tel: '',
addressData: ChinaAddressData,
addressValue: ['北京市', '市辖区', '朝阳区'],
addressValueDetail: '',
postcode: '',
}
},
methods: {
sendAddrFn() {
let regName=/^[a-zA-Z\u4E00-\u9FA5]+$/;
let regTel = /^1[3|4|5|7|8][0-9]{9}$/;
let addrData = {
acceptName:this.name,
mobile:this.tel,
gender:this.sex,
areas:this.addressValue,
address0:this.addressValueDetail,
postcode:this.postcode
}
if(!regName.test(this.name)){
this.$vux.toast.show({
type:'cancel',
text:'亲，收货人姓名填写有误哦~',
width: '16em'
});
} else if(!regTel.test(this.tel)) {
this.$vux.toast.show({
type:'cancel',
text:'亲，收货人电话填写有误哦~',
width: '16em'
});
} else {

comTools.showLoading(this, '保存中...');
setTimeout((data) => {

comTools.hideLoading(this);
if(true){
this.$vux.toast.show({
type:'success',
text:'保存成功~',
width: '7.5em'
});
setTimeout(() => {
this.$router.go(-1);
}, 3000)
}
})
}
},
getAddressName () {
let addressValue = this.addressValue;
return value2name(addressValue, ChinaAddressData);
},
},
}



